import {createCustomElement, actionTypes} from '@servicenow/ui-core';
const {COMPONENT_DOM_READY} = actionTypes;

/**
 * A trigger element to be used with an now-collapse element
 * for triggering the expand/collapse behavior. The `controls` property
 * links the trigger to the corresponding collapse by element ID.
 *
 * ```jsx
 * <now-collapse-trigger controls="collapseID">
 *   <button>Toggle</button>
 * </now-collapse-trigger>
 *
 * <now-collapse id="collapseID">
 * </now-collapse>
 * ```
 *
 * @seismicElement now-collapse-trigger
 * @summary A trigger element to be used with an now-collapse element for triggering the expand/collapse behavior.
 */
createCustomElement('now-collapse-trigger', {
	properties: {
		/**
		 * ID of the `now-collapse` element associated with this trigger.
		 * @type {'string'}
		 */
		controls: {schema: {type: 'string'}, required: true},
		/** @private */
		ariaControls: {
			reflect: true,
			computed: (state) => state.properties.controls,
			schema: {type: 'string'}
		},
		/** @private */
		ariaExpanded: {
			reflect: true,
			default: 'false',
			schema: {type: 'string'}
		},
		/** @private */
		role: {default: 'button', reflect: true, schema: {type: 'string'}}
	},
	slots: {
		/**
		 * Place content inside of the default slot of the collapse trigger to use it
		 * as a control for a now-collapse component instance.
		 * @defaultSlot
		 */
		defaultSlot: {}
	},
	actionHandlers: {
		[COMPONENT_DOM_READY]: ({host, updateProperties, properties}) => {
			const collapse = host.parentNode.querySelector(`#${properties.controls}`);
			if (!collapse) return;

			if (collapse.expanded) {
				updateProperties({ariaExpanded: 'true'});
			}
		}
	},
	eventHandlers: [
		{
			events: ['click'],
			effect: ({host, updateProperties, properties}) => {
				const collapse = host.parentNode.querySelector(
					`#${properties.controls}`
				);
				if (!collapse) return;

				collapse.expanded = !collapse.expanded;
				updateProperties({ariaExpanded: collapse.expanded ? 'true' : 'false'});
			},
			modifier: {name: 'throttle', limit: 300}
		}
	],
	view: () => <slot />
});
