import {createCustomElement} from '@servicenow/ui-core';
import styles from './_now-collapse.scss';

const view = (state) => {
	const {expanded} = state.properties;
	return (
		<div class={{'now-collapse': true, 'is-expanded': expanded}}>
			<slot />
		</div>
	);
};

/**
 * Component that lets the user expand and collapse sections. 
 * ```jsx
 * <now-collapse expanded>
 *   Rich content area to be expanded and collapsed.
 * </now-collapse>
 * ```
 *
 * @seismicElement now-collapse
 * @summary A helper element for expandable and collapsible sections.
 * @uib.label Collapse
 * @uib.icon collapse
 * @uib.description A helper element for expandable and collapsible sections
 * @uib.category primitives
 * @uib.associatedTypes global.core
 * @uib.properties expanded, ariaHidden
 */
createCustomElement('now-collapse', {
	properties: {
		/**
		 * When true, the collapsible section is currently expanded.
		 * @type {boolean}
		 * @uib.description Whether the collapsible section is currently expanded
		 * @uib.defaultValue true
		 */
		expanded: {default: false, schema: {type: 'boolean'}},
		/**
		 * When true, the collapsible section is visible to a screen reader.
		 * It is only visible to a screen reader when expanded.
		 * @type {string}
		 * @uib.label ARIA hidden
		 * @uib.description Whether the collapsible section is visible to a screen reader
		 */
		ariaHidden: {
			computed: (state) => (state.properties.expanded ? 'false' : 'true'),
			reflect: true,
			schema: {type: 'string', enum: ['true', 'false']}
		}
	},
	slots: {
		/**
		 * Content placed in the default slot appears inside of the collapsible section.
		 * @defaultSlot
		 */
		defaultSlot: {}
	},
	styles,
	view
});
